<template lang="pug">
	.drop-list(v-bind:class="{opened: open}", v-click-outside="handleFocusOut")
		.visible(v-on:click="open = !open")
			p.biger {{ active }}
		.list
			span(v-for="item in list", v-on:click="select(item)", v-bind:class="{text: type == 'tooltip'}") {{ item.name }}
</template>

<script>
	export default {
		props: {
			list: [Object, Array],
			default: String,
			type: String
		},
		data() {
			return {
				active: this.default,
				open: false
			}
		},
		methods: {
			select(data){
				if (this.type != 'tooltip') {
					this.open = false;
					this.active = data.name;
					this.$emit('input', data);
				}
			},
			handleFocusOut() {
				this.open = false;
			}
		}
	}
</script>