<template lang="pug">
	.dashboard(v-bind:class="{ 'animate-dashboard': lastPartAnimation, 'user-company': selectedCampaign.kind == 'copy' }")
		.client-and-campaing-info
			.client
				span Client: 
				p {{ selectedCampaign.campaignClient }}

			.campaign
				span Campaign: 
				p {{ selectedCampaign.name }}

		.logo-and-cards
			.campaign-info(v-if="dataLoad")
				.first-card
					.date-card
						span.card-title End Date:
						p(v-if="selectedCampaign.kind == 'standard'") {{ campaignData.end_date.split('/')[0] }}
							span /
							| {{ campaignData.end_date.split('/')[1] }}
							span /
							| {{ campaignData.end_date.split('/')[2] }}

						p(v-else) --
							span /
							| --
							span /
							| ----

				.second-card
					.leads-stat
						.til
							p Remaining Leads / Lead Goal

						.from-to(v-if="selectedCampaign.kind == 'standard'")
							p {{ total_main.remaining_leads }}
							p.blue.slash / 
							p.blue {{ total_main.leads_goal }}

						.from-to(v-else)
							p 0
							p.blue.slash / 
							p.blue 0

				.third-card
					.leads-stat
						.til
							p Leads Generated:

						.from-to(v-if="selectedCampaign.kind == 'standard'")
							p(v-if="campaignData.total_goal != 0 ") {{  Number(total_main.percent_completion).toFixed(0) }}%
							p(v-else) 0%

						.from-to(v-else)
							p 0%

				.four-card
					.user-manager(v-on:click="contactManager()", v-bind:class="{active: RightSidebar.contactM}")
						.photo(v-if="campaignData.managed_by.photo_url")
							img(
								:src="campaignData.managed_by.photo_url",
								alt=""
							)

						.photo.no-photo(v-else)

						.desc
							.by
								p Managed by

							.name
								p {{ campaignData.managed_by.name }}

			.main-logo(
				v-bind:class="{'anim-logo': logoAnim}"
			)

				img(src="@/assets/logo-dashboard.svg", alt="")
		
		.user-campaign
			h4 Your campaign is being launched...

		.hours-block
			.all-statistic(v-if="dataLoad && selectedCampaign.kind == 'standard'")
				.database
					.left-side
						p.lt Current DataBase
						p.lb INFUSEmedia

					.right-side
						p.rt 138+ million

				.target
					.left-side
						p.lt(v-on:click="createTASector()") Targeted contacts
						p.lb
							span(v-if="RightSidebar.state == 'running'") Engagement in Process
							span(v-else) Engagement Paused

					.right-side(v-if="RightSidebar.total_audience")
						//- p.rt 8+ million 
						p.rt(v-if="RightSidebar.total_audience < 999999") {{ RightSidebar.total_audience.toLocaleString('en-US') }}
						p.rt(v-else) {{ RightSidebar.total_audience.toString().substring(0,1) }}+ million
						p.rb(v-if="campaignData.engagement_in_process_value") {{ engagement_in.toLocaleString('en-US') }}

				.verified
					.left-side
						router-link(to="/settings#lets_verify", target="_blank").link-verified Verified
						p.lb In Process of Validation

					.right-side
						p.rt {{ total_main.leads_generated }}
						p.rb {{ inProcess }}

				.lead-goal-c
					.left-side
						span.lead-goal(v-on:click="showLeadPopup()") Lead Goal
						p.rb {{ total_main.leads_goal }}

				router-link(to="/settings", target="_blank").leads-percent-block
					.leads-percent
						.single.top(v-bind:class="{empty: !targetsHours.top.percent}")
							.candle
								.fill(v-bind:style="{ height: targetsHours.top.percent + '%'}")
							.info
								p.value Top
								.b-line
								p.percent(v-if="targetsHours.top.percent") {{ targetsHours.top.percent }}%
								p.not(v-else) Not Ordered

						.single.middle(v-bind:class="{empty:! targetsHours.middle.percent}")
							.candle
								.fill(v-bind:style="{ height: targetsHours.middle.percent + '%'}")
							.info
								p.value Middle
								.b-line
								p.percent(v-if="targetsHours.middle.percent") {{ targetsHours.middle.percent }}%
								p.not(v-else) Not Ordered

						.single.bottom(v-bind:class="{empty: !targetsHours.bottom.percent}")
							.candle
								.fill(v-bind:style="{ height: targetsHours.bottom.percent + '%'}")
							.info
								p.value Bottom
								.b-line
								p.percent(v-if="targetsHours.bottom.percent") {{ targetsHours.bottom.percent }}%
								p.not(v-else) Not Ordered

			.hours(
				v-bind:class="{'first-step-anim': firstAnim, 'second-step-anim': secondAnim}"
			)

