						leads_generated: 0,
						percent: false
					},
					middle: {
						leads_goal: 0,
						leads_generated: 0,
						percent: false
					},
					bottom: {
						leads_goal: 0,
						leads_generated: 0,
						percent: false
					}
				},
				TAS: {
					ABM: {
						delta: 0,
						defaultStatus: null
					},
					SuppressionList: {
						delta: 0,
						defaultStatus: null
					},
					JobTitle: {
						delta: 0,
						defaultStatus: null
					},
					Industries: {
						delta: 0,
						defaultStatus: null
					},
					Geo: {
						delta: 0,
						defaultStatus: null
					},
					Revenue: {
						delta: 0,
						defaultStatus: null
					},
					CompanySize: {
						delta: 0,
						defaultStatus: null
					},
