				new_velocity: 0,
				engagement_in: 0
			}
		},
		created: function(){
			// this.runHour();
			// window.SelectC.init();
			setTimeout(() => this.startFirstAnim(), 1000)
		},
		computed: {
			...mapGetters(['RightSidebar']),
			SC(){
				return this.$store.getters.SC.id;
			}
		},
		watch: {
			SC(){
				// this.RS = this.$store.state.SC;
				this.selectedCampaign = this.$store.state.SelectedCampaign;
				this.loadCampaignData();
			},
			'RightSidebar.trigger': function () {
				this.changeTA();
			}
			// SelectedCampaign(){
			// 	this.selectedCampaign = this.$store.state.SelectedCampaign;
			// }
		},
		methods:{
			...mapMutations(['setRS', 'updateRS']),
			resetAnim(){
				// this.firstAnim = false
				// this.secondAnim = false
				// this.logoAnim = false
				// this.topAnim = false
				// this.bottomAnim = false
				// setTimeout(() => this.startFirstAnim(), 3000)
				this.lastPartAnimation = false;
				setTimeout(() => {
					this.lastPartAnimation = true;
				}, 3000);
			},
			startFirstAnim(){
				this.logoAnim = true;
				this.firstAnim = true;
				setTimeout(() => this.secondAnim = true, 1000)
				setTimeout(() => {
					this.topAnim = true;
					this.bottomAnim = true
				} , 2000);
				setTimeout(() => {
					this.animationFinished = true;
					this.loadCampaignData()
				}, 4000);
			},
			calculateLeads(){
				this.reverseAnimation = true;
				this.hideSquares = true;
				setTimeout(() => {
					this.lastPartAnimation = true;
					this.createHours();
				}, 500);
				setTimeout(() => {
					this.reverseAnimation = false;
				}, 1100);
				setInterval(() => this.generateLeads(), 60000)
			},
			loadCampaignData(){
				if (this.animationFinished && this.selectedCampaign.id) {
					var id = this.selectedCampaign.id;
					window.api.get('campaign/'+id+'/hourglass')
						.then(campaign => {
							this.campaignData = campaign.data;
							this.calculateLeads();
							this.logoAnim = false;
							this.dataLoad = true;
							this.calcTotal();
							var st = {
								sections: campaign.data.sections,
								tactics: campaign.data.tactics,
								state: campaign.data.state
							}
							this.setRS(st);
							this.createTASector();
							this.integrateWith = campaign.data.integration_type.name;
							this.engagement_in = +campaign.data.engagement_in_process_value + (campaign.data.base_velocity * 2);
							setTimeout(() => {this.rainAnimation()}, 500)
							// this.fillTop();
						})
						.catch(campaignError => {
							console.log(campaignError);
						})
				}
			},
			changeTA(){
				var baseAudience = this.campaignData.audience_targeted;
				var zero = 0;
				// this.baseFill = Number(this.campaignData.ta_volume).toFixed(0);
				this.baseFill = 0;

				for (var single in this.RightSidebar.sections) {
					var sectorDelta = 0;

					if(this.TAS[this.RightSidebar.sections[single].slug]){
						sectorDelta = this.TAS[this.RightSidebar.sections[single].slug].delta;

						if (!this.RightSidebar.sections[single].enabled) {
							if (this.TAS[this.RightSidebar.sections[single].slug].defaultStatus != this.RightSidebar.sections[single].enabled) {
								if (sectorDelta > 0) {
									baseAudience = baseAudience - Math.abs(sectorDelta);
									this.baseFill = this.baseFill - 1;
								}else{
									baseAudience = baseAudience + Math.abs(sectorDelta);
									this.baseFill = this.baseFill + 1;
								}
							}
						}else{
							if (this.TAS[this.RightSidebar.sections[single].slug].defaultStatus != this.RightSidebar.sections[single].enabled) {
								if (sectorDelta > 0) {
									baseAudience = baseAudience + Math.abs(sectorDelta);
									this.baseFill = this.baseFill + 1;
								}else{
									baseAudience = baseAudience - Math.abs(sectorDelta);
									this.baseFill = this.baseFill - 1;
								}
							}
						}
					}
				}

				var velocity = this.campaignData.base_velocity;

				for (var item in this.RightSidebar.sections) {
					if (this.RightSidebar.sections[item].enabled) {
						velocity += this.RightSidebar.sections[item].delta_v_sector;
					}
				}

				this.new_velocity = velocity;
				this.engagement_in = +this.campaignData.engagement_in_process_value + (velocity * 2)

				zero += baseAudience;

				this.updateRS(['total_audience', zero]);
			},
			createTASector(){

				this.workingLines = this.campaignData.ta_volume;

				this.TAS.ABM = {
					delta: Number(this.campaignData.audience_targeted * ((this.RightSidebar.sections.find(item => item.slug === 'ABM').delta_ta_sector) / 100)).toFixed(0),
					defaultStatus: this.RightSidebar.sections.find(item => item.slug === 'ABM').enabled
				};

				this.TAS.SuppressionList = {
					delta: Number(this.campaignData.audience_targeted * ((this.RightSidebar.sections.find(item => item.slug === 'SuppressionList').delta_ta_sector) / 100)).toFixed(0),
					defaultStatus: this.RightSidebar.sections.find(item => item.slug === 'SuppressionList').enabled
				};

				this.TAS.JobTitle = {
					delta: Number(this.campaignData.audience_targeted * ((this.RightSidebar.sections.find(item => item.slug === 'JobTitle').delta_ta_sector) / 100)).toFixed(0),
					defaultStatus: this.RightSidebar.sections.find(item => item.slug === 'JobTitle').enabled
				};

				this.TAS.Industries = {
					delta: Number(this.campaignData.audience_targeted * ((this.RightSidebar.sections.find(item => item.slug === 'Industries').delta_ta_sector) / 100)).toFixed(0),
					defaultStatus: this.RightSidebar.sections.find(item => item.slug === 'Industries').enabled
				};

				this.TAS.Geo = {
					delta: Number(this.campaignData.audience_targeted * ((this.RightSidebar.sections.find(item => item.slug === 'Geo').delta_ta_sector) / 100)).toFixed(0),
					defaultStatus: this.RightSidebar.sections.find(item => item.slug === 'Geo').enabled
				};

				this.TAS.Revenue = {
					delta: Number(this.campaignData.audience_targeted * ((this.RightSidebar.sections.find(item => item.slug === 'Revenue').delta_ta_sector) / 100)).toFixed(0),
					defaultStatus: this.RightSidebar.sections.find(item => item.slug === 'Revenue').enabled
				};

				this.TAS.CompanySize = {
					delta: Number(this.campaignData.audience_targeted * ((this.RightSidebar.sections.find(item => item.slug === 'CompanySize').delta_ta_sector) / 100)).toFixed(0),
					defaultStatus: this.RightSidebar.sections.find(item => item.slug === 'CompanySize').enabled
				};

				this.TAS.ITCurated = {
					delta: Number(this.campaignData.audience_targeted * ((this.RightSidebar.sections.find(item => item.slug === 'ITCurated').delta_ta_sector) / 100)).toFixed(0),
					defaultStatus: this.RightSidebar.sections.find(item => item.slug === 'ITCurated').enabled
				};

				this.updateRS(['TAS', this.TAS]);

				var baseAudience = this.campaignData.audience_targeted;

				this.updateRS(['total_audience', baseAudience]);

				var maxAudience = 0;
				for(var item in this.TAS){
					if ((!this.TAS[item].defaultStatus && this.TAS[item].delta > 0) || (this.TAS[item].defaultStatus && this.TAS[item].delta < 0)) {
							var dl = Math.abs(this.TAS[item].delta)
							maxAudience = +maxAudience + +dl;
					}
				}

				var velocity = this.campaignData.base_velocity;

				for (var single in this.RightSidebar.sections) {
					if (this.RightSidebar.sections[single].enabled) {
						velocity += this.RightSidebar.sections[single].delta_v_sector;
					}
				}

				this.new_velocity = velocity;
				this.engagement_in = +this.campaignData.engagement_in_process_value + (velocity * 2)

				this.updateRS(['max_audience', (+maxAudience + +baseAudience)]);
			},
			getSquares(from, to){
				var squaresLines = [];
				for (var s = from; s <= to; s++) {
					squaresLines.push(this.squares[s])
				}
				return squaresLines;
			},
			createHours(){
				this.targetsHours = {
					top: {
						leads_goal: 0,
						leads_generated: 0,
						percent: false,
					},
					middle: {
						leads_goal: 0,
						leads_generated: 0,
						percent: false,
					},
					bottom: {
						leads_goal: 0,
						leads_generated: 0,
						percent: false,
					}
				}

				this.$refs.['line-1'].classList.remove('line-t', 'line-m', 'line-b');
				this.$refs.['line-2'].classList.remove('line-t', 'line-m', 'line-b');
				this.$refs.['line-3'].classList.remove('line-m', 'line-m', 'line-b');

				this.$refs.['line-fill-one'].classList.remove('line-t', 'line-m', 'line-b');
				this.$refs.['line-fill-two'].classList.remove('line-t', 'line-m', 'line-b');
				this.$refs.['line-fill-three'].classList.remove('line-t', 'line-m', 'line-b');

				var top = false;
				var middle = false;
				var bottom = false;

				var stringCount = 0;

				for(var item in this.campaignData.targets){
					if (this.campaignData.targets[item].grade == 'Top') {
						this.targetsHours.top.leads_goal += +Number(this.campaignData.targets[item].leads_goal).toFixed(0);
						this.targetsHours.top.leads_generated += +Number(this.campaignData.targets[item].leads_generated).toFixed(0);
						if (!top) {
							stringCount += 1;
						}
						top = true;
					}
					if (this.campaignData.targets[item].grade == 'Middle') {
						this.targetsHours.middle.leads_goal += +Number(this.campaignData.targets[item].leads_goal).toFixed(0);
						this.targetsHours.middle.leads_generated += +Number(this.campaignData.targets[item].leads_generated).toFixed(0);
						if (!middle) {
							stringCount += 1;
						}
						middle = true;
					}
					if (this.campaignData.targets[item].grade == 'Bottom') {
						this.targetsHours.bottom.leads_goal += +Number(this.campaignData.targets[item].leads_goal).toFixed(0);
						this.targetsHours.bottom.leads_generated += +Number(this.campaignData.targets[item].leads_generated).toFixed(0);
						if (!bottom) {
							stringCount += 1;
						}
						bottom = true;
					}
				}

				for (var single in this.targetsHours) {
					if (this.targetsHours[single].leads_goal != 0) {
						this.targetsHours[single].percent = Number(this.targetsHours[single].leads_generated / this.targetsHours[single].leads_goal * 100).toFixed(0);
					}
				}

				this.workingStrings = this.campaignData.dashboard_string_count;

				this.$refs.['line-1'].innerHTML = '';
				this.$refs.['line-2'].innerHTML = '';
				this.$refs.['line-3'].innerHTML = '';

				this.$refs.['line-fill-one'].innerHTML = '';
				this.$refs.['line-fill-two'].innerHTML = '';
				this.$refs.['line-fill-three'].innerHTML = '';

				var emptyLines = [];

				for(var d = 0; d <= (5 - this.workingStrings); d++){
					emptyLines.push(this.squares[d])
				}

				this.$refs.['line-1'].innerHTML = emptyLines;
				this.$refs.['line-1'].classList.add('empty');

				var from = 6 - Number(this.workingStrings).toFixed(0);
				var to = this.squares.length - 1;

				if (stringCount == 3) {

					this.$refs.['line-fill-one'].classList.add('line-t', 'animate');
					this.$refs.['line-fill-two'].classList.add('line-m', 'animate');
					this.$refs.['line-fill-three'].classList.add('line-b', 'animate');

					// top 500
					// middle 600
					// bottom 600

					if (this.targetsHours.top.leads_goal > this.targetsHours.middle.leads_goal) {
						if (this.targetsHours.top.leads_goal > this.targetsHours.bottom.leads_goal) {
							this.$refs.['line-fill-one'].innerHTML = this.getSquares(from, 3);
							this.$refs.['line-fill-two'].innerHTML = this.getSquares(4, 4);
							this.$refs.['line-fill-three'].innerHTML = this.getSquares(5, 5);
						}else{
							this.$refs.['line-fill-one'].innerHTML = this.getSquares(from, from);
							this.$refs.['line-fill-two'].innerHTML = this.getSquares((from + 1), (from + 1));
							this.$refs.['line-fill-three'].innerHTML = this.getSquares((from + 2), 5);
						}
					}else if(this.targetsHours.middle.leads_goal > this.targetsHours.bottom.leads_goal){
						this.$refs.['line-fill-one'].innerHTML = this.getSquares(from, from);
						this.$refs.['line-fill-two'].innerHTML = this.getSquares((from + 1), (4));
						this.$refs.['line-fill-three'].innerHTML = this.getSquares(5, 5);
					}else{
						if (this.workingStrings == 6) {
							this.$refs.['line-fill-one'].innerHTML = this.getSquares(0, 1);
							this.$refs.['line-fill-two'].innerHTML = this.getSquares(2, 3);
							this.$refs.['line-fill-three'].innerHTML = this.getSquares(4, 5);
						}else if(this.workingStrings == 5){
							this.$refs.['line-fill-one'].innerHTML = this.getSquares(1, 2);
							this.$refs.['line-fill-two'].innerHTML = this.getSquares(3, 4);
							this.$refs.['line-fill-three'].innerHTML = this.getSquares(5, 5);
						}else if(this.workingStrings == 4){
							this.$refs.['line-fill-one'].innerHTML = this.getSquares(2, 3);
							this.$refs.['line-fill-two'].innerHTML = this.getSquares(4, 4);
							this.$refs.['line-fill-three'].innerHTML = this.getSquares(5, 5);
						}else if(this.workingStrings <= 3){
							this.$refs.['line-fill-one'].innerHTML = this.getSquares(3, 3);
							this.$refs.['line-fill-two'].innerHTML = this.getSquares(4, 4);
							this.$refs.['line-fill-three'].innerHTML = this.getSquares(5, 5);
						}
					}
					this.fillTop('line-fill-one');
					this.fillMiddle('line-fill-two');
					this.fillBottom('line-fill-three');
				}else if(stringCount == 2){
					var otherEnd;

					if (this.workingStrings == 6) {
						otherEnd = 3;
					}else if(this.workingStrings == 5){
						otherEnd = 3;
					}else if(this.workingStrings == 4){
						otherEnd = 3;
					}else if(this.workingStrings == 3){
						otherEnd = 4;
					}else if(this.workingStrings == 2){
						otherEnd = 4;
					}


					if (top && middle) {
						this.$refs.['line-fill-one'].classList.add('line-t', 'animate');
						this.$refs.['line-fill-two'].classList.add('line-m', 'animate');

						if (this.targetsHours.top.leads_goal > this.targetsHours.middle.leads_goal) {
							this.$refs.['line-fill-one'].innerHTML = this.getSquares(from, otherEnd);
							this.$refs.['line-fill-two'].innerHTML = this.getSquares((otherEnd + 1), to);
						}else{
							this.$refs.['line-fill-one'].innerHTML = this.getSquares(from, (otherEnd - 1));
							this.$refs.['line-fill-two'].innerHTML = this.getSquares((otherEnd), to);
						}
						this.fillTop('line-fill-one');
						this.fillMiddle('line-fill-two');
					}else if(top && bottom){
						this.$refs.['line-fill-one'].classList.add('line-t', 'animate');
						this.$refs.['line-fill-two'].classList.add('line-b', 'animate');

						if (this.targetsHours.top.leads_goal > this.targetsHours.bottom.leads_goal) {
							this.$refs.['line-fill-one'].innerHTML = this.getSquares(from, otherEnd);
							this.$refs.['line-fill-two'].innerHTML = this.getSquares((otherEnd + 1), to);
						}else{
							this.$refs.['line-fill-one'].innerHTML = this.getSquares(from, (otherEnd - 1));
							this.$refs.['line-fill-two'].innerHTML = this.getSquares((otherEnd), to);
						}
						this.fillTop('line-fill-one');
						this.fillBottom('line-fill-two');
					}else{
						this.$refs.['line-fill-one'].classList.add('line-m');
						this.$refs.['line-fill-two'].classList.add('line-b');

						if (this.targetsHours.middle.leads_goal > this.targetsHours.bottom.leads_goal) {
							this.$refs.['line-fill-one'].innerHTML = this.getSquares(from, otherEnd);
							this.$refs.['line-fill-two'].innerHTML = this.getSquares((otherEnd + 1), to);
						}else{
							this.$refs.['line-fill-one'].innerHTML = this.getSquares(from, (otherEnd - 1));
							this.$refs.['line-fill-two'].innerHTML = this.getSquares((otherEnd), to);
						}

						this.fillMiddle('line-fill-one');
						this.fillBottom('line-fill-two');
					}
				}else if(stringCount == 1){
					this.$refs.['line-fill-one'].innerHTML = this.getSquares(from, to);

					if (top) {
						this.$refs.['line-fill-one'].classList.add('line-t', 'animate');
						this.fillTop('line-fill-one');
					}else if(middle){
						this.$refs.['line-fill-one'].classList.add('line-m', 'animate');
						this.fillMiddle('line-fill-one');
					}else if(bottom){
						this.$refs.['line-fill-one'].classList.add('line-b', 'animate');
						this.fillBottom('line-fill-one');
					}
				}
			},
			fillTop(ref){
				var lenTop = this.$refs.[ref].children.length;
				var topPercentFill = Number((this.targetsHours.top.leads_generated / this.targetsHours.top.leads_goal) * 100).toFixed(0);
				var topPercentOne = Number(100 / lenTop).toFixed(8);
				var topFill = Number(topPercentFill / topPercentOne).toFixed(0);

				var offset = lenTop - topFill;

				for (var t = 0; t <= topFill - 1; t++) {
					this.$refs.[ref].children[offset + t].classList.add('fill');
				}
			},
			fillMiddle(ref){
				var lenMiddle = this.$refs.[ref].children.length;
				var middlePercentFill = Number((this.targetsHours.middle.leads_generated / this.targetsHours.middle.leads_goal) * 100).toFixed(0);
				var middlePercentOne = Number(100 / lenMiddle).toFixed(8);
				var middleFill = Number(middlePercentFill / middlePercentOne).toFixed(0);

				var offset = lenMiddle - middleFill;

				for (var m = 0; m <= middleFill - 1; m++) {
					this.$refs.[ref].children[offset + m].classList.add('fill');
				}
			},
			fillBottom(ref){
				var lenBottom = this.$refs.[ref].children.length;
				var bottomPercentFill = Number((this.targetsHours.bottom.leads_generated / this.targetsHours.bottom.leads_goal) * 100).toFixed(0);
				var bottomPercentOne = Number(100 / lenBottom).toFixed(8);
				var bottomFill = Number(bottomPercentFill / bottomPercentOne).toFixed(0);

				var offset = lenBottom - bottomFill;

				for (var b = 0; b <= bottomFill - 1; b++) {
					this.$refs.[ref].children[offset + b].classList.add('fill');
				}
			},
			generateLeads(){
				if(this.RightSidebar.state == 'running'){
					for(var item2 in this.campaignData.targets){
						if (this.campaignData.targets[item2].grade == 'Top') {
							this.targetsHours.top.leads_goal += +Number(this.campaignData.targets[item2].leads_goal).toFixed(0);
							this.targetsHours.top.leads_generated += +Number(this.campaignData.targets[item2].leads_generated).toFixed(0);
						}
						if (this.campaignData.targets[item2].grade == 'Middle') {
							this.targetsHours.middle.leads_goal += +Number(this.campaignData.targets[item2].leads_goal).toFixed(0);
							this.targetsHours.middle.leads_generated += +Number(this.campaignData.targets[item2].leads_generated).toFixed(0);
						}
						if (this.campaignData.targets[item2].grade == 'Bottom') {
							this.targetsHours.bottom.leads_goal += +Number(this.campaignData.targets[item2].leads_goal).toFixed(0);
							this.targetsHours.bottom.leads_generated += +Number(this.campaignData.targets[item2].leads_generated).toFixed(0);
						}
					}

					for (var single in this.targetsHours) {
						if (this.targetsHours[single].leads_goal != 0) {
							this.targetsHours[single].percent = Number(this.targetsHours[single].leads_generated / this.targetsHours[single].leads_goal * 100).toFixed(0);
						}
					}

					var max = 0;
					var next = 0;
					var stringVelocity = Number(this.new_velocity / this.campaignData.targets.length).toFixed(0)
					for(var item in this.campaignData.targets){
						max = Number(+this.campaignData.targets[item].leads_goal * 0.99).toFixed(0);
						next = Number(+this.campaignData.targets[item].leads_generated + +stringVelocity).toFixed(0);
						var check = max - next;
						if (check > 0) {
							this.campaignData.targets[item].leads_generated = next;
							this.campaignData.targets[item].remaining_leads = Number(this.campaignData.targets[item].leads_goal - next).toFixed(0);
							this.campaignData.targets[item].percent_completion = Number(this.campaignData.targets[item].leads_generated / this.campaignData.targets[item].leads_goal * 100).toFixed(0);
						}else{
							this.campaignData.targets[item].leads_generated = max;
							this.campaignData.targets[item].remaining_leads = this.campaignData.targets[item].leads_goal - max;
							this.campaignData.targets[item].percent_completion = Number(this.campaignData.targets[item].leads_generated / this.campaignData.targets[item].leads_goal * 100).toFixed(0);
						}
					}
					this.createHours();
					// this.updateRS(['targets', this.GetSettings.targets]);
					this.calcTotal();
				}
			},
			sortFunc: function (obj){
				return obj.sort((a, b) => a.id - b.id );
			},
			showIntegratePopup(){
				this.showIntegrate = true;
			},
			showLeadPopup(){
