			},
			calcTotal(){
				this.total_main = {
					leads_goal: 0,
					percent_completion: 0,
					leads_generated: 0,
					remaining_leads: 0
				}

				for (var single in this.campaignData.targets) {
					this.total_main.leads_goal += +this.campaignData.targets[single].leads_goal;

					this.total_main.leads_generated += +this.campaignData.targets[single].leads_generated

					this.total_main.remaining_leads += +this.campaignData.targets[single].remaining_leads

					if (this.campaignData.targets[single].grade == "Top") {
						this.emptyTop = false;
					}
					if (this.campaignData.targets[single].grade == "Middle") {
						this.emptyMiddle = false;
					}
					if (this.campaignData.targets[single].grade == "Bottom") {
						this.emptyBottom = false;
					}
				}

				this.total_main.percent_completion = Math.round(this.total_main.leads_generated / this.total_main.leads_goal * 100);

				var sum = Math.round(this.total_main.remaining_leads / this.total_main.leads_goal * 100)
				var percent;

				if (this.campaignData.pacing_type.name == 'even') {
					percent = 30;
				}else{
					percent = 40;
				}

				var newPercent = Math.round(sum * percent / 100);

				this.inProcess = Number(this.total_main.leads_goal * (newPercent / 100)).toFixed(0);

			},
