<template lang="pug">
	.main-layer
		LeftSidebar(
			v-if="showLeft"
			v-model="activeCampaign"
		)

		SettingsSidebar(
			v-if="showLeftSettings"
		)

		AddCampaign(
			v-bind:class="{active: addCampaign}"
			v-if="showLeft"
		)

		IntegratePopup(
			v-if="showIntegrate"
		)

		LeadGoal(
			v-if="editGoal"
		)

		router-view

		RightSidebar(
			:activeCampaign="activeCampaign"
			v-if="showRight"
		)

</template>

<style lang="scss">
	@import "./mainStyles/main.scss";
</style>

<script>
	import LeftSidebar from '@/components/Left-Sidebar'
	import RightSidebar from '@/components/Right-Sidebar'
	import AddCampaign from '@/components/Add-Campaign'
	import IntegratePopup from '@/components/Integrate-Popup'
	import LeadGoal from '@/components/Lead-Goal'
	import SettingsSidebar from '@/components/Settings-Sidebar'
	export default {
		name: 'App',
		components: {
			LeftSidebar,
			RightSidebar,
			AddCampaign,
			IntegratePopup,
			LeadGoal,
			SettingsSidebar
		},
		data(){
			return{
				activeCampaign: null,
				showLeft: false,
				showLeftSettings: false,
				showRight: false,
				addCampaign: false,
				showIntegrate: false,
				editGoal: false
			}
		},
		computed: {
			AddCampaign() {
				return this.$store.getters.AddCampaign
			}
		},
		created: function(){
			this.checkRouter();
		},
		watch: {
			AddCampaign(){
				this.NewCampaign();
			},
			$route() {
				this.checkRouter();
			}
		},
		methods:{
			NewCampaign(){
				var st = this.$store.state.AddCampaign;
				var status = st.status === 'true';
				if(status){
					this.addCampaign = true;
				}else{
					this.addCampaign = false;
				}
			},
			checkRouter(){
				if (this.$route.path == '/') {
					this.showRight = true;
					this.showLeft = true;
					this.showLeftSettings = false;
				}else if (this.$route.path == '/add-new-campaign') {
					this.showLeft = true;
					this.showRight = false;
					this.showLeftSettings = false;
				}else if(this.$route.name == 'Settings'){
					this.showRight = false;
					this.showLeft = false;
					this.showLeftSettings = true;
				}else{
					this.showRight = false;
					this.showLeft = false;
					this.showLeftSettings = false;
				}
			}
		}
	}
</script>
