<template lang="pug">
	div.card-box
		.card.nurturing(v-bind:class="{disable: !cardStatus}")
			CardHeader(
				:status="cardStatus",
				title="Nurturing",
				v-model="cardStatus"
				type="tooltip",
				slug="Nurturing",
				:tooltipText="tooltipText"
