<template lang="pug">
	.dropbox(v-bind:class="{opened: open}", v-click-outside="handleFocusOut")
		.visible(v-on:click="open = !open")
			p.biger {{ active.answer != null ? active.answer : active }}
		.box-list
			span(v-for="item in list", v-on:click="select(item)") {{ item.answer }}
</template>

<script>
	export default {
		props: {
			list: [Object, Array],
			default: [Object, Array, String] // Schrdinger's cat
		},
		data() {
			return {
				active: this.default,
				open: false
			}
		},
		watch: {
			default:{
				handler: function() {
					this.active = this.default
				},
				deep: true
			}
		},
		methods: {
			select(data){
				this.open = false;
				this.active = data;
				this.$emit('input', data);
			},
			handleFocusOut() {
				this.open = false;
			}
		}
	}
</script>