<template lang="pug">
	div.card-box
		.lv-card
			.statistic
				.item
					.lv-logo
						img(src="@/assets/s-lv-logo.svg", alt="")

				.item
					.info.associates
						h5 {{ associates }} Associates 
						p Assigned to this Project

				.item
					.info.green
						//- h5 {{ Number(GetSettings.doughnutPercents[0] / 100 * GetSettings.total_main.leads_goal).toFixed(0) }}
						h5 {{ Number(GetSettings.total_main.leads_generated).toFixed(0) }}
						p Validated and Delivered 
							br
							| to Client

				.item
					.info.blue
						h5 {{ Number(+Number(GetSettings.doughnutPercents[0] / 100 * GetSettings.total_main.leads_goal).toFixed(0) + +GetSettings.rejected).toFixed(0) }}
						p Total Leads
							br
							| Generated

				.item
					.info.orange
						h5 {{ Number(GetSettings.doughnutPercents[1] / 100 * GetSettings.total_main.leads_goal).toFixed(0) }}
						p In Process 
							br
							| of Validation

				.item
					.info.red
						h5 {{ Number(GetSettings.rejected).toFixed(0) }}
						p Rejected 
							br
							| Leads

			.teams
				h6 View the Team:

				.teams-list
					vuescroll(:ops="ops")
						.list
							.teams-row.first-row
								.team(v-for="(item, index) in this.GetSettings.teams", v-if="index < teamsInRow")
									.photos
										.single-photo.team-lead(v-if="item.team_lead")
											img(:src="item.team_lead.photo_url", alt="")
											.tooltip-lead
												h6 Team Lead
												i Team Performance
												p
													span.green {{ Number(item.delivered).toFixed(0) }}
													| /
													span.yellow {{ Number(item.rejected).toFixed(0) }}

										.single-photo(v-if="item.team_member1")
											img(:src="item.team_member1.photo_url", alt="")

										.single-photo(v-if="item.team_member2")
											img(:src="item.team_member2.photo_url", alt="")

										.single-photo(v-if="item.team_member3")
											img(:src="item.team_member3.photo_url", alt="")

										.single-photo(v-if="item.team_member4")
											img(:src="item.team_member4.photo_url", alt="")

									h4 {{ item.name }}

							.teams-row.second-row
								.team(v-for="(item, index) in this.GetSettings.teams", v-if="index >= teamsInRow")
									.photos
										.single-photo.team-lead(v-if="item.team_lead")
											img(:src="item.team_lead.photo_url", alt="")
											.tooltip-lead
												h6 Team Lead
												i Team Performance
												p
													span.green {{ Number(item.delivered).toFixed(0) }}
													| /
													span.yellow {{ Number(item.rejected).toFixed(0) }}

										.single-photo(v-if="item.team_member1")
											img(:src="item.team_member1.photo_url", alt="")

										.single-photo(v-if="item.team_member2")
											img(:src="item.team_member2.photo_url", alt="")

										.single-photo(v-if="item.team_member3")
											img(:src="item.team_member3.photo_url", alt="")

										.single-photo(v-if="item.team_member4")
											img(:src="item.team_member4.photo_url", alt="")

									h4 {{ item.name }}
</template>

<script>
	import vuescroll from 'vuescroll'

	import { mapGetters } from 'vuex'

	export default {
		name: 'LV',
		data() {
			return {
				ops:{
					bar:{
						background: '#3DA3DB',
						onlyShowBarOnScroll: false,
						keepShow: true,
						size: '2px'
					},
					rail:{
						background: '#BCBEC0',
						opacity: 1,
						size: '2px'
					}
				},
				teamsInRow: 0,
				associates: 0
			}
		},
		components: {
			vuescroll
		},
		created(){
			this.divideTeams();
			this.getCount();
		},
		computed: { ...mapGetters(['GetSettings']) },
		methods: {
			divideTeams(){
				this.teamsInRow = Math.floor(this.GetSettings.teams.length / 2);
			},
			getCount(){
				for(var item in this.GetSettings.teams){
					var person = this.GetSettings.teams[item];
					if (person.team_lead) {
						this.associates += 1;
					}
					if (person.team_member1) {
						this.associates += 1;
					}
					if (person.team_member2) {
						this.associates += 1;
					}
					if (person.team_member3) {
						this.associates += 1;
					}
					if (person.team_member4) {
						this.associates += 1;
					}
				}
			}
		}
	}
</script>